body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  /* background-image: linear-gradient(155deg , #0a5aa5 5%, #000000 30% ,black 60%); */
  background-image: linear-gradient(155deg , #155794 10% , #000000 90% ,#ff8717 10%);
  background-color: black;
  background-repeat: no-repeat;
  min-height: 100vh;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.full{
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;

}
.header,.works,.footer{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.header,.footer{
  height: 25vh;
}
.header{
  transform: translateY(-50%);
}
.works{
  min-height: 50vh;
}
.me{
  width: 90px;
  height: 90px;
  border-radius: 10px;
  background-color: white;
}
.skilles{
  color: #cccccc;
}
.work-img{
  width: 70px;
  height: 70px;
  border-radius: 12px;
  box-shadow: 0 0 8px #0a5aa5;
}
.tool{
  color: #9b9b9b;
  font-style: italic;
}
.list{
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.item{
  margin: 10px;
}
.item{
  text-transform: none;
  text-decoration: none;
  color: white;
}
.links{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 200px;
  
}
.link{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.link img{
  position: absolute;
  background-color: rgba(0, 0, 0, 0);
  width: 28px;
  height: 28px;
  border-radius: 3px;
}

.side{
  width: 28px;
  height: 28px;
  background-color:  #0a5aa500;
  z-index: -1;
  position: absolute;
  border-radius: 3px;
}
.tit{
  color: #fa973a;
  
}
.footer:first-child{
  color: red;
}
.water{
  position: absolute;
  right: 20px;
  bottom: 20px;
  transform: rotate(-25deg);
}
@media screen and (max-width: 380px) {
  .list{
    flex-direction: column;
  }
}
@media screen and (min-width: 381px) {
  .list{
    width: 200px;
    flex-wrap: wrap;
    flex-direction: row;
  }
}

.null{
  transform: translateY(-50%);
  transform: translateY(-50%);
  color:  #2369aa;
}